import Login from './views/Login.vue'
import NotFound from './views/404.vue'
import Home from './views/Home.vue'
import Main from './views/Main.vue'

import sell from './views/reports/sell.vue'
import develop from './views/reports/develop.vue'
import purchase from './views/reports/purchase.vue'
import artist from './views/reports/artist.vue'
import ebaysell from './views/reports/ebaysell.vue'
import trending from './views/reports/trending.vue'
import fixed from './views/reports/fixed.vue'
import operate from './views/reports/operate.vue'
import dollar from './views/reports/dollar.vue'
import aaa from './views/reports/aaa.vue'

let routes = [
    {
        path: '/login',
        component: Login,
        name: '',
        hidden: true
    },
    {
        path: '/404',
        component: NotFound,
        name: '',
        hidden: true
    },
    //{ path: '/main', component: Main },
    {
        path:'/',
        component: Home,
        name: '',
        iconCls: 'el-icon-message',
        children:[
            { path: '/sell', component: sell, name: ''},
            { path: '/develop', component:develop, name: ''},
            { path: '/purchase', component:purchase, name: ''},
            { path: '/artist', component:artist, name: ''},
            { path: '/ebaysell', component:ebaysell, name: 'ebay'},
            { path: '/trending', component:trending, name: ''},
            { path: '/fixed', component:fixed, name: ''},
            { path: '/operate', component:operate, name: ''},
            { path: '/dollar', component:dollar, name: ''},
            { path: '/aaa', component:aaa, name: ''},
        ]
    },
    {
        path: '*',
        hidden: true,
        redirect: { path: '/404' }
    }
];

export default routes;


